<template>
  <div>
    <div class="content-section introduction">
      <div class="feature-intro">
        <h1>Galleria <span>Responsive</span></h1>
        <p>Galleria responsiveness is defined with the <b>responsiveOptions</b> property.</p>
      </div>
    </div>

    <div class="content-section implementation">
      <div class="card">
        <Galleria
          :value="images"
          :responsiveOptions="responsiveOptions"
          :numVisible="7"
          :circular="true"
          style="max-width: 800px">
          <template #item="slotProps">
            <img :src="slotProps.item.itemImageSrc" :alt="slotProps.item.alt" style="width: 100%; display: block" />
          </template>
          <template #thumbnail="slotProps">
            <img :src="slotProps.item.thumbnailImageSrc" :alt="slotProps.item.alt" style="display: block" />
          </template>
        </Galleria>
      </div>
    </div>

    <GalleriaResponsiveDoc />
  </div>
</template>

<script>
import PhotoService from '../../service/PhotoService'
import GalleriaResponsiveDoc from '@/doc/galleria/Responsive.vue'

export default {
  components: { GalleriaResponsiveDoc },
  data() {
    return {
      images: null,
      responsiveOptions: [
        {
          breakpoint: '1024px',
          numVisible: 5
        },
        {
          breakpoint: '960px',
          numVisible: 4
        },
        {
          breakpoint: '768px',
          numVisible: 3
        },
        {
          breakpoint: '560px',
          numVisible: 1
        }
      ]
    }
  },
  galleriaService: null,
  created() {
    this.galleriaService = new PhotoService()
  },
  mounted() {
    this.galleriaService.getImages().then(data => this.images = data)
  }
}
</script>
